<template>
    <div class="post_item" ref="postItem">

        <router-link
            v-if="isHomePage"
            class="img_wrapper"
            :to="{
                name: 'Post',
                params: {
                    id: post.id,
                    title: post.title
                }
            }"
        >
            <img :src="require(`../assets/${post.img}`)" class="post_img" />
        </router-link>

        <div v-else class="img_wrapper post_img_wrapper">
            <img :src="require(`../assets/${post.img}`)" class="post_img" />
        </div>

        <div :class="`content_wrapper ${postPageClass}`">
            <a href="#" class="category">{{ getCategoryName }}</a>
            <router-link 
                class="post_title homePage_title"
                v-if="isHomePage"
                :to="{
                name: 'Post',
                params: {
                    id: post.id,
                    title: post.title
                }
            }"
            >
                {{ post.title }}
            </router-link>
            <p v-else class="post_title">{{ post.title }}</p>
            <p class="post_date">{{ post.date }}</p>
            <p class="post_content">{{ post.text }}</p>
        </div>
    </div>
</template>

<script>
export default {
    name: "PostItem",
    props: ["post", 'isHomePage', 'isPostPage'],
    computed: {
        // 獲取類別名稱
        getCategoryName() {
            let categoryList = this.$store.state.category;
            for (const category of categoryList) {
                if (this.post.categoryId === category.id) {
                    return category.name;
                }
            }
            return "其他類別";
        },
        // 回傳post Page的class
        postPageClass(){
            return this.isPostPage ? 'post_content_wrapper' : '';
        }
    },
    methods: {
        // 超過50字隱藏功能
        textHide(){
            const maxLength = 50;
            const allPostText = document.querySelectorAll(".post_content");
            for (const postText of allPostText) {
                if (postText.innerHTML.trim().length > maxLength) {
                    let maxLengthText = postText.innerHTML.substr(0, maxLength + 1);
                    postText.innerHTML = `${maxLengthText}...`;
                }
            }
        },
    },
    mounted() {
        // 當list頁面才隱藏，內頁則全顯示
        if (this.isHomePage) this.textHide();
    },
};
</script>

<style lang="scss" scoped>
.post_item {
    display: flex;
    margin-bottom: 20px;
}
.img_wrapper {
    display: block;
    width: 40%;
    margin-right: 15px;

    .post_img {
        width: 100%;
        transition: box-shadow 0.3s;

        &:hover{
            box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5);;
        }
    }
}

.post_img_wrapper{
    width: 100%;

    .post_img:hover{
        box-shadow: none;
    }
}

.content_wrapper {
    font-size: 14px;
    align-self: center;
    width: 60%;

    .category {
        display: inline-block;
        text-decoration: none;
        color: #000;
        background-color: #ccc;
        padding: 5px 5px 1px;
        vertical-align: middle;
        // margin: auto 0;
        transition: transform 0.3s;

        &:hover{
            transform: scale(1.1);
        }
    }

    .post_title {
        display: block;
        text-decoration: none;
        color: #000;
        font-weight: bold;
        font-size: 16px;
        margin: 15px auto 5px;
        transition: color 0.3s;
    }

    .homePage_title:hover{
        color: darkgoldenrod;
    }

    .post_date {
        margin-bottom: 20px;
    }

    .post_content {
        word-wrap: break-word;
        line-height: 1.2;
    }
}

// 內頁用
.post_content_wrapper{
    width: 100%;
    margin-top: 20px;
}

@media screen and (max-width: 1024px) {
    .post_item {
        display: block;
        margin: 0 auto;
        margin-bottom: 30px;

        .img_wrapper {
            margin: 0 auto;
            width: 90%;
        }

        .content_wrapper {
            margin: 0 auto;
            margin-top: 10px;
            width: 90%;
        }
    }
}

@media screen and (max-width: 767.98px) {
    .post_item {
        position: relative;
        flex: 100% 0 0;

        .content_wrapper{
            height: 124px;
        }
    }
}
</style>
