<template>
    <div class="home">
        <transition-group tag="div" :name="transitionName" class="post_wrapper">
            <PostItem
                v-for="(post, index) in postData"
                :key="post.id"
                :post="post"
                ref="postItem"
                v-show="slideShow(index)"
                :isHomePage="isHomePage"
            ></PostItem>
        </transition-group>
        <div class="btn_wrapper">
            <div class="prev_btn" @click="slideCtrl(-1)" ref="prev_btn">
                <i class="fa-solid fa-angle-left"></i>
            </div>
            <div class="next_btn _on" @click="slideCtrl(1)" ref="next_btn">
                <i class="fa-solid fa-angle-right"></i>
            </div>
        </div>
    </div>
</template>

<script>
import PostItem from "../components/PostItem";
export default {
    name: "Home",
    components: { PostItem },
    data() {
        return {
            postData: [],
            // 顯示幾筆
            maxPost: 6,
            // 輪播目前正在顯示的item的index
            showPostIndex: 0,
            // 過場名稱
            transitionName: "",
            // 是否以點擊
            clickWait: false,
            timer: {},
            isHomePage: true
        };
    },
    methods: {
        // 上下一張按鈕功能
        slideCtrl(changeIdx) {
            if (this.clickWait) {
                return;
            }
            this.stopTime();
            this.clickWait = true;
            switch (true) {
                case changeIdx === -1 && this.showPostIndex === 0:
                case changeIdx === 1 && this.showPostIndex === this.postData.length - 1:
                    this.setTime();
                    break;

                default:
                    this.showPostIndex = this.showPostIndex + changeIdx;
                    this.setTime();
                    break;
            }
        },

        // 讓輪播僅<768時可使用
        slideShow(postIndex) {
            // <768時 且 只讓當前index的item顯示
            if (window.innerWidth < 768) {
                if (postIndex === this.showPostIndex) {
                    return true;
                } else {
                    return false;
                }
            } else if (window.innerWidth >= 768) {
                // 當
                this.showPostIndex = 0;
                return true;
            }
        },

        // 重製是否點擊
        setTime() {
            this.timer = setTimeout(() => {
                this.clickWait = false;
            }, 300);
        },

        // 中止
        stopTime() {
            clearTimeout(this.timer);
        },

        // 取得資料
        getPostData() {
            let tempData = this.$store.state.posts;

            // 刪除 分類ID 非10或11 的post
            for (let i = 0; i < tempData.length; i++) {
                let isShow = false;

                switch (tempData[i].categoryId) {
                    case "10":
                    case "11":
                        isShow = true;
                        break;
                }

                if (!isShow) {
                    tempData.splice(i, 1);
                    i--;
                }
            }

            // 陣列中的日期排序
            tempData.sort((x, y) => {
                let prevDate = new Date(x.date);
                let nextDate = new Date(y.date);
                return Date.parse(nextDate) - Date.parse(prevDate);
            });

            // 只取前6筆
            while (tempData.length > this.maxPost) {
                tempData.pop();
            }

            this.postData = tempData;
        },
    },
    watch: {
        // 當 新index > 舊index 則為右進(按下一張)，反之(按上一張)
        showPostIndex(nIdx, oIdx) {
            // 判斷是否移到第一張或最後一張，並改變按鈕樣式
            if (nIdx === 0) {
                this.$refs.prev_btn.classList.remove("_on");
                this.$refs.next_btn.classList.add("_on");
            } else if (nIdx === this.postData.length - 1) {
                this.$refs.next_btn.classList.remove("_on");
            } else {
                this.$refs.prev_btn.classList.add("_on");
                this.$refs.next_btn.classList.add("_on");
            }
            this.transitionName = nIdx > oIdx ? "right-in" : "left-in";
        },
    },
    mounted() {
        this.getPostData();
        // 瀏覽器縮放
        window.onresize = () => {
            // 強制解析，避免到下一張縮放後，其他會不顯示
            this.$forceUpdate();
            this.slideShow();
        };
    },
};
</script>

<style lang="scss" scoped>
.post_wrapper {
    max-width: 960px;
    margin: 0 auto;
}

.btn_wrapper {
    display: none;
}


@media screen and (min-width: 768px) and (max-width: 1024px) {
    .post_wrapper {
        display: grid;
        grid-template-columns: calc((100% - 20px) / 2) calc((100% - 20px) / 2);
        gap: 20px;
    }
}

@media screen and (max-width: 767.98px) {
    .post_wrapper {
        overflow: hidden;
        display: flex;
    }
    .btn_wrapper {
        display: flex;
        margin: 0 auto;
        width: 30%;
        font-size: 20px;
        margin-bottom: 20px;
        justify-content: space-around;

        div {
            border: 1px solid #000;
            width: 20px;
            height: 20px;
            text-align: center;
            padding: 14px;
            opacity: 0.3;
        }

        ._on {
            cursor: pointer;
            opacity: 1;
            &:hover {
                background-color: #ccc;
            }
        }
    }

    // transitionName(Next):'right-in'
    .right-in-enter {
        left: 0%;
    }
    .right-in-enter-to {
        left: -100%;
    }

    .right-in-enter-active,
    .right-in-leave-active {
        transition: left 0.3s;
    }

    .right-in-leave {
        left: 0%;
    }
    .right-in-leave-to {
        left: -100%;
    }

    // transitionName(Prev):'left-in'
    .left-in-enter {
        right: 100%;
    }
    .left-in-enter-to {
        right: 0%;
    }
    .left-in-enter-active,
    .left-in-leave-active {
        transition: right 0.3s;
    }
    .left-in-leave {
        right: 100%;
    }
    .left-in-leave-to {
        right: 0%;
    }
}
</style>
