<template>
    <div>
        <PostItem :post="getThisPost" :isPostPage="isPostPage"></PostItem>
    </div>
</template>

<script>
import PostItem from "../components/PostItem";
export default {
    name: 'Post',
    components:{PostItem},
    props: ['id', 'title'],
    data() {
        return {
            isPostPage: true
        }
    },
    computed: {
        getThisPost(){
            for (const post of this.$store.state.posts) {
                if(post.id === this.id){
                    return post;
                }
            }
            return '錯誤';
        }
    },
}
</script>

<style lang="scss" scoped>
.post_item{
    display: block;
    max-width: 720px;
    margin: 0 auto 50px;
}
</style>